@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100;200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@font-face {
  font-family: Avenir;
  src: url(/static/media/AvenirLTStd-Medium.adcbfbb8.otf)
}

:root {
  --white: #E3E3E4;
  --blue: #6892ee;
  --lightgrey: #F8F8F8;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Avenir;
}
/* App */
.App {
  height: 95vh;
  width: 100vw;
  padding: 1.5rem 2rem 0 5rem;
  padding-bottom: 0;
  background-color: #F8F8F8;
  background-color: var(--lightgrey);
  /* display: flex;
  justify-content: space-between;
  flex-wrap: wrap; */
}

/* Header */
.header-container {
  background-color: #6892ee;
  background-color: var(--blue);
  height: 50px;
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.14), 
        0px 1px 10px 0px rgba(0,0,0,0.12), 
        0px 2px 4px -1px rgba(0,0,0,0.2);
  position: relative;
  width: 100%;
}
.header-container > h1 {
  line-height: 50px;
  padding: 0 2rem;
  align-self: center;
  color: #E3E3E4;
  color: var(--white);
  font-size: 28px;
}
/* General Selectors */
h2 {
  color: #9d9d9d;
  font-size: 28px;
  text-align: center;
  border-bottom: 0.1px solid #9d9d9d;
  padding: 0 0 .7rem 0;
}

/* Sidebar */
.sidebar {
  background-color: #E3E3E4;
  background-color: var(--white);
  height: 95vh;
  min-width: 50px;
  position: absolute;
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.14), 
        0px 1px 10px 0px rgba(0,0,0,0.12), 
        0px 2px 4px -1px rgba(0,0,0,0.2);
}
.sidebar ul {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  margin: 0;
}
.sidebar ul  li {
  list-style: none;
}
.icon-sidebar {
  font-size: 24px;
  color: #9d9d9d;
  
}
.card-todo,
.card-pomodoro,
.card-notes {
  padding: 1rem;
}
.card-todo {
  width: 45%;
  box-shadow: 0px 2px 5px 0px rgba(19, 68, 203, 0.14), 
        0px 1px 10px 0px rgba(0,0,0,0.12), 
        0px 2px 4px -1px rgba(0,0,0,0.2);
  border: none !important;
  float: right;
  height: 96%;
}
.todos-container {
  display: flex;
}
.todos-container input{
  width: 50%;
}
.todos-container select{
  width: 30%;
}
.todos-container button{
  width: 20%;
  background-color: var(--blue);
  border-color: var(--blue);
}
.todo-item:hover {
  text-decoration:line-through;
  cursor: pointer;
}
.todo-container {
  display: flex;
  width: 100%;
  padding: 2rem 0;
}
.todo-container .todo-item {
  width: 80%;
}
.todo-container a {
  width: 20%;
}
.todo-container a button{
  width: 100%;
  height: 100%;
}
.card-pomodoro {
  width: 53%;
  height: 48%;
  box-shadow: 0px 2px 5px 0px rgba(19, 68, 203, 0.14), 
        0px 1px 10px 0px rgba(0,0,0,0.12), 
        0px 2px 4px -1px rgba(0,0,0,0.2);
  border: none !important;
  position: relative;
  float: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card-pomodoro  h2 {
  margin-bottom: 0 ;
}
.pomodoro-timer {
  display: flex;
  justify-content: space-between;
  height: 100%;
}
.pomodoro-timer > section {
  border: 1px solid rgb(208, 208, 208);
  border-radius: 3px;
  align-self: center;
  text-align: center;
  padding: .5rem;
  min-width: 30%;
  height: 90%;
}
.session-time {
  border: 1px solid black;
  border-radius: 100%;
  padding: 1.2rem 1rem;
}
.session-card {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}

.card-notes {
  width: 53%;
  margin-top: 1rem;
  height: 45%;
  box-shadow: 0px 2px 5px 0px rgba(19, 68, 203, 0.14), 
        0px 1px 10px 0px rgba(0,0,0,0.12), 
        0px 2px 4px -1px rgba(0,0,0,0.2);
  border: none !important;
  position: relative;
  float: left;
}
